body {
  background:linear-gradient(180deg,rgb(255 253 246),rgb(255 253 249) 42%,rgb(255 250 247))
}
.underline{
  margin-top: 125px;
  padding-bottom: 10px;
  border-bottom:rgb(62, 62, 62)solid 2px;
}
.underline h2{
  font-size: 36px;
}
.project-image {
  opacity: 1;
  display: block;
  transition: 0.5s ease;
  backface-visibility: hidden;
}
.project-image img {
  width: 250px;
  height: 250px;
}
.bodytext li {
  font-family: "Arial", sans-serif;
  padding-bottom: 15px;
  line-height: 150%;
  font-size: 1.06em;
  color: #2b372b;
}
h1,
h2,
h3 {
  -webkit-font-smoothing: subpixel-antialiased;
}
p {
  font-family: "Arial", sans-serif;
  font-size: 1.06em;
  line-height: 150%;
  color: #2b372b;
}

hr {
  border-color: rgb(200, 255, 173);
  background-color: rgb(255, 173, 176);
  border-style: solid;
  margin-top: 2%;
  padding-top: 0px;
  margin-bottom: 20px;
  text-decoration: none;
}
.left .row img{
  height: auto; 
  width: auto; 
  max-width: 300px; 
  max-height: 200px;
padding:10px;
}
h2 {
  margin-bottom: 7px;
}
/* Thumbnail Stuff */
.codecontent .project-title {
  margin-top: 20px;
}
.project-code {
  position: relative;
}
.content {
  transition: 0.5s ease;
  position: relative;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: left;
}
.codecontent {
  text-align: left;
  transition: none;
  opacity: 1;
}
.buttons {
  position: relative;
  left: 50%;
  top: -6.5%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  border-radius: 20px;
}
.buttons a {
  transition: 0.5s ease;
  opacity: 0;
  background-color: rgb(255, 242, 242);
  color: rgb(204, 111, 111);
  font-size: 16px;
  padding: 12px;
  display: block;
}
.project:hover .buttons a {
  opacity: 1;
}
#DesignProjs {
justify-content: left;
max-width: 940px;
}
.project-title {
  color: rgba(63, 124, 110, 1);
}
.project-description {
  color: rgb(116 116 116);
  margin-top:4px;
}
#archivedesc {
  text-align: center;
  padding-left: 250px;
  padding-right: 250px;
}
.project:hover .project-image {
  color: #2d572d;
  opacity: 0.3;
}

.project:hover .content {
  opacity: 1;
}
.project-title {
  margin-top: 30px;
  font-size: 20px;
}

.footer {
  margin-top: 45px;
  margin-bottom: 15px;
}
.footer p {
  text-align: center;
  font-size: 12px;
  font-style: italic;
  padding-top: 0px;
  padding-bottom: 0px;
