  margin-bottom: 2px;
}
.footer a {
  font-size: 138%;
}
.nochange a {
  font-size: 100%;
}
.nochange{
  font-size:16px;
}
a {
  text-decoration: none;
  color: rgba(63, 124, 110, 1);
}
a:hover {
  color: #172117;
}
@keyframes fade {
  from {
    color: rgba(63, 124, 110, 1);
  }
  to {
    color: #172117;
  }
}
.projmargins {
