}
.item:hover {
  color: black;
  animation: none;
}
.navigation-sub {
  margin-left: auto;
}
.navigation-sub a {
  margin-left: 2vw;
  display: inline;
  font-size: 1em;
}
.header {
  text-align: left;
}
.item {
  color: black;
  font-size: 18px;
  background-repeat: no-repeat;
}
a.item {
