  padding: 0px;
  line-height: 1.2;
  font-size: 2em;
}
/* stickynav */
li.is-current {
  font-weight: 800;
}
.sticky {
  position: sticky;
  top: 0;
}
.sticky > nav {
  position: absolute;
  align-self: start;
  margin-top: 20px;
}
.section-nav {
  position: fixed;
  z-index: 1;
  right: 25px;
  overflow-x: hidden;
  padding-left: 0;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  max-width: 200px;
  width: 90%;
}
.section-nav a {
  font-size: 14px;
  color: rgb(113, 113, 113);
}
.section-nav a:hover {
  font-weight: 900;
  color: rgb(113, 113, 113);

}
.section-nav li {
  list-style-type: none;
  padding-bottom: 8px;
}
.section-nav ul {
  padding-inline-start: 10px;
}
@media only screen and (max-width: 1479px) {

}
@media only screen and (width: 2560px) {
  .projmargins{
